import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import moment_ru from 'moment/locale/ru';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Header from '@vkontakte/vkui/dist/components/Header/Header';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import SimpleCell from '@vkontakte/vkui/dist/components/SimpleCell/SimpleCell';
import ContentCard from '@vkontakte/vkui/dist/components/ContentCard/ContentCard';
import CardGrid from '@vkontakte/vkui/dist/components/CardGrid/CardGrid';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Link from '@vkontakte/vkui/dist/components/Link/Link';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import PullToRefresh from '@vkontakte/vkui/dist/components/PullToRefresh/PullToRefresh';
import Banner from '@vkontakte/vkui/dist/components/Banner/Banner';

import { Icon24Filter } from '@vkontakte/icons';


moment.locale('ru');

const Home = ({ id, go, list, refresh, isFetching }) => (
	<Panel id={id}>
		<PanelHeader>Clientlist</PanelHeader>
		<Banner
	        mode="image"
	        header="Мы ВКонтакте"
	        subheader="Подписывайся на группу"
	        background={
	          <div
	            style={{
	              backgroundColor: '#5b9be6',
	              backgroundImage: 'url(https://sun9-31.userapi.com/PQ4UCzqE_jue9hAINefBMorYCdfGXvcuV5nSjA/eYugcFYzdW8.jpg)',
	              backgroundPosition: 'right bottom',
	              backgroundSize: '102%',
	              backgroundRepeat: 'no-repeat',
	            }}
	          />
	        }
	        actions={<Link href={"https://vk.com/clientlist_ru"}><Button style={{ marginTop: '15px', marginBottom: '10px' }} mode="overlay_primary">Перейти в группу</Button></Link>}
      	/>
		<PullToRefresh onRefresh={refresh} isFetching={isFetching}>
	    {list && list.map(item => {
			return (
				<Link href={"https://vk.com/id" + item.client.public_id}>
					<ContentCard
					  style={{ marginLeft: '10px', marginRight: '10px', marginBottom: '10px' }}
		              subtitle={ moment.unix(item.public_date).fromNow() }
		              header={<SimpleCell
		              	style={{ marginLeft: '-10px'}}
						description={ item.tags.map((e) => `#${e}`).join(' ') }
			          	before={<Avatar style={{ marginLeft: '-10px'}} src={item.client.avatar}/>}
			        >{item.client.name}
			        </SimpleCell>}
		              caption={item.text}
		            />
		        </Link>
			);
	    })}
	    </PullToRefresh>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	list: PropTypes.array
};

export default Home;
